import axios from 'axios'
import identity from './identity'

/**
 * @description  Http 封装类
 * @param webapp {appKey:'',site:''}
 * @constructor
 */
function Http(webapp) {
    this.init(webapp);
}

Http.prototype.init = function (webapp) {

    this.$axios = axios.create();
    let identityHandler = new identity(webapp);

    //添加请求拦截器
    this.$axios.interceptors.request.use(config => {
        config.headers.Authorization = "Bearer " + identityHandler.getToken();
        //15秒超时
        config.timeout = 15000;
        return config
    }, error => {
        return Promise.reject(error)
    });

    //添加响应拦截器
    this.$axios.interceptors.response.use(
        response => {
            return response
        },
        error => {
            if (error.response) {
                switch (error.response.status) {
                    case 401:
                        identityHandler.authorize();
                        break;
                    default:
                        break;
                }
            }
            // 返回接口返回的错误信息
            return Promise.reject(error);
        }
    );
};

export default Http


