import weui from 'weui.js'
import config from './config'
import utils from './utils'
import store from 'store'
import wxsdk from 'weixin-js-sdk'
import _ from 'lodash'
import formTemplate from './template/form.art'
import timelineTemplate from './template/timeline.art'
import attachmentTemplate from './template/attachment.art'

/**
 * @description 表单对象构造函数
 * @constructor
 */
function Form() {
    this.data = {};
}

/**
 * @description 初始表单信息
 * @param url
 * @private
 */
function init(url, options) {
    //TODO form init 待优化 2018年1月12日20:00:38
    let loading = weui.loading('加载中');
    let containerElement = document.getElementById('container');
    let loadmoreElement = document.getElementById('loadmore');
    $axios.get(url)
        .then(response => {
            let data = response.data || {FlowInstance: {Title: undefined}, CurrentStep: undefined};
            this.data = {instance: data.FlowInstance, step: data.CurrentStep};
            this.data.photos = data.Attachments.filter(attachment => {
                return attachment.IsPhoto === true && attachment.Path;
            });

            store.set("flow_data", this.data);
            createForm(data, options.formId);
            createAttachment(data, options.attachmentId);
            createTimeLine(data, options.timelineId);

            utils.renderTimego('.comments-time', null);
            utils.setTitle(data.FlowInstance.Title, -1);
            loadmoreElement.remove();
            loading && loading.hide();
            containerElement.style.opacity = "1";
        })
        .catch(err => {
            loading && loading.hide();
            alert(JSON.stringify(err));
            $(loadmoreElement).html('<span class="weui-loadmore__tips">暂无数据</span>');
            console.error(err)
        });
}

/**
 * @description 创建附件html
 * @param data
 * @param elId
 */
function createAttachment(data, elId) {
    var html = attachmentTemplate(data);
    setHtml(elId, html);
}

/**
 * @description 创建表单html
 * @param data
 * @param elId
 */
function createForm(data, elId) {
    setHtml(elId, formTemplate(data));
}

/**
 * @description 生成时间轴
 */
function createTimeLine(data, elId) {
    var stepsVM = [];
    var exitDo = false;
    try {
        let flowInstance = data.FlowInstance;
        let nodeExtends = data.NodeExtends;
        let steps = flowInstance.StepInstanceList;
        let nodes = flowInstance.NodeInstanceList;
        //为步骤附加节点信息
        for (let s = 0; s < steps.length; s++) {
            let step = steps[s];
            step.nodes = [];
            for (let n = 0; n < nodes.length; n++) {
                if (step.StepInstanceGuid != nodes[n].StepInstanceGuid)
                    continue;
                let node = nodes[n];
                //获取节点的扩展信息 交办 和 协商
                if (step.CanAssign || step.CanTransfer) {
                    node.extendNodeInfo = getExtendNodeInfo(node, nodeExtends);
                }
                step.nodes.push(node);
            }
        }
        //拼接时间轴对象
        for (let si = 0; si < steps.length; si++) {
            let tempSteps = [];
            let tempStep = steps[si];
            let surplus = [];
            let virtualStep = null;

            if (exitDo) break;
            switch (tempStep.StepType) {
                case config.stepCategory.start:
                    tempStep.patch = "first";
                    tempStep.bgColor = "bg-green";
                    tempStep.bColor = "bg-green";
                    if (si != 0) {
                        tempStep.patch = "";
                        tempStep.noBefore = false;
                    }
                    break;
                case config.stepCategory.end:
                    tempStep.patch = "last";
                    tempStep.noBefore = false;
                    tempStep.bgColor = "bg-gray";
                    tempStep.bColor = "bg-gray";
                    break;
                default:
                    tempStep.bgColor = "bg-gray";
                    tempStep.bColor = "bg-gray";
                    break;
            }
            if (tempStep.nodes && tempStep.nodes.length)
                for (let ni = 0; ni < tempStep.nodes.length; ni++) {
                    let tempNode = tempStep.nodes[ni];
                    tempNode.FlowHandleTypeName = _getHandleMethodName(tempNode.FlowHandleType);
                    if (tempNode.HandleMode == 2 && tempNode.NodeStatus != -2) continue;//系统处理的节点不显示
                    //1.节点为已处理
                    //2.步骤处理模式为串行
                    if (tempNode.NodeStatus != 0 && tempNode.NodeStatus != 1) {//已处理
                        virtualStep = _.assign({}, tempStep);
                        //状态 步骤头 节点
                        virtualStep.bgColor = "bg-green";
                        virtualStep.bColor = "bg-green";
                        if (tempNode.NodeStatus == -2) {
                            virtualStep.bgColor = "bg-red";
                            virtualStep.bColor = "bg-red";
                            virtualStep.patch = "last";
                            virtualStep.toVoid = true;
                            //终止下一步解析
                            exitDo = true;
                        }
                        virtualStep.headerHide = ni ? true : false;
                        virtualStep.nodes = [tempNode];
                        tempSteps.push(virtualStep);

                    } else if (tempStep.HandleMode == config.stepHandleMethod.serial) {//串行未处理
                        virtualStep = _.assign({}, tempStep);
                        //状态 步骤头 节点
                        if (tempNode.NodeStatus == config.nodeState.toWait) {
                            virtualStep.bgColor = "bg-yellow";
                            virtualStep.bColor = "bg-yellow";
                        }
                        virtualStep.headerHide = ni ? true : false;
                        virtualStep.nodes = [tempNode];
                        tempSteps.push(virtualStep);

                    } else {//并行且未处理
                        tempNode.headerHide = ni ? true : false;
                        tempNode.bgColor = "bg-gray";
                        tempNode.bColor = "bg-gray";
                        if (tempNode.NodeStatus == config.nodeState.toWait) {
                            tempNode.bgColor = "bg-yellow";
                            tempNode.bColor = "bg-yellow";
                        }
                        surplus.push(tempNode);
                    }
                    if (exitDo) break;
                }

            //并行且未处理的 特殊处理
            if (surplus.length) {
                virtualStep = tempStep;
                virtualStep.nodes = surplus;
                virtualStep.each = true;
                virtualStep.headerHide = surplus[0].headerHide;
                virtualStep.bgColor = surplus[0].bgColor;
                tempSteps.push(virtualStep);
            }

            stepsVM = stepsVM.concat(tempSteps);
        }
    } catch (e) {
        alert("解析审批记录失败1：" + e.message);
        stepsVM = [];
    }
    try {
        if (stepsVM && stepsVM.length !== 0) {
            let htmlStr = timelineTemplate({steps: stepsVM})
            setHtml(elId, htmlStr);
        }
    }
    catch (e) {
        alert("解析审批记录失败2：" + e.message);
    }
}

/**
 * @description 设置html
 * @param elId
 * @returns {HTMLElement | null}
 */
function setHtml(elId, htmlStr) {
    let el = document.getElementById(elId);
    el && (el.innerHTML = htmlStr);
}

//获取扩展节点信息
function getExtendNodeInfo(node, nodeExtend) {
    var extendNodes = {};
    if (nodeExtend && nodeExtend.length)
        for (var k = 0; k < nodeExtend.length; k++) {
            var currExtNode = nodeExtend[k];
            //协商和回复协商
            if (currExtNode.NodeExtendType == 3 || currExtNode.NodeExtendType == 2) {
                if (currExtNode.NodeInstanceGuid != node.NodeInstanceGuid || !currExtNode.HandleDatetime) continue;
                var extNodes = extendNodes[node.NodeInstanceGuid] || [];
                switch (currExtNode.NodeExtendType) {
                    case 2: //发起

                        var extNode = {
                            title: '发起协商',
                            content: currExtNode.HandlerSuggestion,
                            senderId: currExtNode.HandlerGuid,
                            recipientId: currExtNode.AuditorGuid,
                            sender: currExtNode.HandlerName,
                            recipient: currExtNode.AuditorName,
                            extra: currExtNode
                        };
                        extNodes.push(extNode);
                        extendNodes[node.NodeInstanceGuid] = extNodes;
                        break;
                    case 3: //回复

                        extNodes.forEach(function (extNode, index, self) {
                            if (extNode.recipientId == currExtNode.HandlerGuid) {
                                if (!extNode.comments)
                                    extNode.comments = [];
                                var comment = {
                                    title: '回复',
                                    content: currExtNode.HandlerSuggestion,
                                    senderId: currExtNode.HandlerGuid,
                                    recipientId: currExtNode.AuditorGuid,
                                    sender: currExtNode.HandlerName,
                                    recipient: currExtNode.AuditorName,
                                    extra: currExtNode
                                };
                                extNode.comments.push(comment);
                            }
                        });
                        break;
                    default:
                        break;

                }
            }
            else if (currExtNode.NodeExtendType >= 5 && currExtNode.NodeExtendType <= 8) {//作废
                if (currExtNode.StepInstanceGuid == node.StepInstanceGuid) {
                    return currExtNode;
                }
            }
        }
    return extendNodes;
}

/**
 * @description 获取处理方法名称
 * @param method
 * @returns {string}
 */
function _getHandleMethodName(method) {
    switch (method) {
        /// <summary>
        /// 同意
        /// </summary>
        case 0:
            return "同意";
            break;
        /// <summary>
        /// 不同意
        /// </summary>
        case 1:
            return "不同意";
            break;
        /// <summary>
        /// 发起协商
        /// </summary>
        case 2:
            return "发起协商";
            break;
        /// <summary>
        /// 授权交办
        /// </summary
        case 3:
            return "授权交办";
            break;
        /// <summary>
        /// 会签
        /// </summary>
        case 4:
            return "会签";
            break;
        /// <summary>
        /// 重新发起
        /// </summary>
        case 5:
            return "重新发起";

            break;
        /// <summary>
        /// 作废
        /// </summary>
        case 6:
            return "作废";

            break;
        /// <summary>
        /// 发起
        /// </summary>
        case 7:
            return "发起";

            break;
        /// <summary>
        /// 结束
        /// </summary>
        case 8:
            return "结束";

            break;
        /// <summary>
        /// 撤回
        /// </summary
        case 9:
            return "同意";
            break;
        /// <summary>
        /// 回复协商
        /// </summary>
        case 21:
            return "回复协商";
            break;
        default:
            return "";
            break;
    }
}

/**
 * @description 处理流程
 * @param payload {object} 处理数据
 * @private
 */
function _handle(payload) {
    payload = payload || {
        Name: undefined,
        HandleType: undefined,
        Extra: {DisagreeHandleType: 1}
    };
    let target = `${payload.Url}?instanceId=${this.data.instance.FlowInstanceGuid}&handletype=${payload.HandleType}&disagreeHandleType=${payload.Extra.DisagreeHandleType}&suggestion=`;
    utils.jumpLink(target);
}

/**
 * @description 附件预览
 * @param target
 */
function attachmentPreview(target) {
    let data = _getAttachmentInfo(target);
    _preview(data);
}

/**
 * @description 预览函数
 * @param data
 * @private
 */
function _preview(data) {
    let officePrefix = "https://view.officeapps.live.com/op/view.aspx?src=";
    let target = null;
    console.log(data);
    if (!data || !data.doctype || !data.href) {
        weui.alert("预览附件失败：" + JSON.stringify(data));
        return
    }
    if (data.isphoto === 'true' || data.isphoto === true) {
        //调用photoswipe插件预览
        //调用微信JSSDK预览
        try {
            let urls = $form.data.photos.map(photo => {
                return photo.Path;
            });
            wxsdk.previewImage({
                current: data.href, // 当前显示图片的http链接
                urls: urls || [] // 需要预览的图片http链接列表
            });
            // wxsdk.chooseImage({
            //     count: 1, // 默认9
            //     sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
            //     sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
            //     success: function (res) {
            //         var localIds = res.localIds; // 返回选定照片的本地ID列表，localId可以作为img标签的src属性显示图片
            //     }
            // });
        }
        catch (e) {
            alert("图片预览失败！" + e.message);
        }
    }
    else if (utils.os().android) {
        if (utils.isOffice(data.doctype)) {
            target = officePrefix + encodeURIComponent(data.href);
            utils.jumpLink(target);
        }
        else if (data.doctype.toLowerCase() == '.pdf') {
            //暂时未模拟
            var url = "http://ebs.highzap.com/Scripts/plugins/pdfjs/web/viewer.html?file=http://ebs.highzap.com/TempUpFiles/1514541197.pdf";
            utils.jumpLink(url);
        }
        else {
            //直接用浏览器打开
            utils.jumpLink(data.href);
        }
    }
    else {//ios
        //直接用浏览器打开
        utils.jumpLink(data.href);
    }
}

/**
 * @description 获取附件信息
 * @param target
 * @returns {{isphoto: boolean, fileid: string, userid: string, href: string, doctype: string}}
 * @private
 */
function _getAttachmentInfo(target) {
    var data = utils.ds(target, ['isphoto', 'fileid', 'userid', 'href', 'doctype']);
    return data;
}

Form.prototype.preview = attachmentPreview;
Form.prototype.init = init;
Form.prototype.flowHandle = _handle;

export default Form


