import _ from 'lodash'
import wxsdk from 'weixin-js-sdk'
import utils from "./utils"

function MailList(target) {
    this.init(target);
    this.bind();
    this.data = {
        userList: [],
        departmentList: []
    };
}

/**
 * @description 初始函数
 * @param target {DOM} element元素
 * @private
 */
function _init(target) {
    this.mailListElement = target;
}

/**
 * @description 添加通讯录
 * @param data
 * @private
 */
function _add(data) {
    let userEl = null,
        avatarEl = null,
        closeEl = null,
        fragment = document.createDocumentFragment(),
        addEl = this.mailListElement.querySelector('a.cc-add');
    try {
        data.forEach((user) => {
            userEl = document.createElement('div');
            avatarEl = document.createElement('img');
            closeEl = document.createElement('a');
            avatarEl.src = user['avatar'];//用户头像
            avatarEl.classList.add('img-item');
            closeEl.classList.add('cc-close');
            closeEl.dataset.userid = user.id;
            closeEl.href = "javascript:;";
            closeEl.innerText = "X";
            userEl.classList.add('cc-item');
            userEl.appendChild(avatarEl);
            userEl.appendChild(closeEl);
            fragment.appendChild(userEl);
        });
        this.mailListElement.innerHTML = "";
        this.mailListElement.appendChild(addEl);
        this.mailListElement.insertBefore(fragment, addEl);
    }
    catch (e) {
        alert(e.message);
    }
}

/**
 * @description 删除通讯录
 * @param target {element} 目标元素
 * @private
 */
function _remove(target) {
    try {
        let parentNode = target.parentNode;
        let userid = utils.ds(target, "userid");
        let evens = _.remove(this.data.userList, (user) => {
            return user.id == userid;
        });
        if (evens.length && evens[0].id == userid) {
            parentNode.remove();
        }
        else {
            alert("删除失败")
        }
    }
    catch (e) {
        alert(e.message);
    }
}

/**
 * @description 加载数据
 * @param data
 * @private
 */
function _loadData(data) {
    if (data && data.length) {
        this.data.userList = data;
        this.add(data);
    }
}

//绑定事件
function _bind() {

    $(this.mailListElement).on('click', 'a.cc-add', () => {
        //已选择人员ID数组
        let selectedUserIds = this.data.userList.map(user => {
            return user.id
        });
        this.invokeMailList({
            selectedUserIds: selectedUserIds
        });
    });

    $(this.mailListElement).on('click', 'a.cc-close', e => {
        this.remove(e.target);
    });
}

/**
 * @description 通过微信SDK唤醒通讯录
 * @param options 参数
 * @private
 */
function _invokeMailList(options) {
    let _this = this;
    let defaults = {
        fromDepartmentId: 0, // 必填，-1表示打开的通讯录从自己所在部门开始展示, 0表示从最上层开始
        mode: 'multi', // 必填，选择模式，single表示单选，multi表示多选
        type: ['user'], // 必填，选择限制类型，指定department、user中的一个或者多个
        selectedDepartmentIds: [], // 非必填，已选部门ID列表。用于多次选人时可重入
        selectedUserIds: [] // 非必填，已选用户ID列表。用于多次选人时可重入
    };
    let opts = _.extend(defaults, options);
    try {
        wxsdk.invoke("selectEnterpriseContact", opts, function (res) {
            if (res['err_msg'] === "selectEnterpriseContact:ok") {
                if (typeof res.result === "string") {
                    //由于目前各个终端尚未完全兼容，需要开发者额外判断result类型以保证在各个终端的兼容性
                    res.result = JSON.parse(res.result)
                }
                let selectedUserList = res.result.userList; // 已选的成员列表
                _this.data.userList = selectedUserList;
                _this.add(selectedUserList);
            } else {
                throw new Error("唤醒通讯录失败！");
            }
        });
    }
    catch (e) {
        alert(e.message);
    }
}

MailList.prototype.add = _add;
MailList.prototype.remove = _remove;
MailList.prototype.init = _init;
MailList.prototype.bind = _bind;
MailList.prototype.loadData = _loadData;
MailList.prototype.invokeMailList = _invokeMailList;

export default MailList


