import weui from 'weui.js'
import store from 'store'
import config from "./config"
import utils from './utils'
import _ from 'lodash'
import MailList from './maillist'
import handleformTemplate from './template/handleform.art'

/**
 * @description 流程处理对象
 * @constructor
 */
function FlowHandle(container, extra, jssdkconfig) {
    this.appendData = undefined;
    this.init(container, extra, jssdkconfig);
}

/**
 * @description 初始数据
 * @param container
 * @param extra
 */
function init(container, extra, jssdkconfig) {
    this.loading = weui.loading('加载中');
    this.jssdkconfig = jssdkconfig;
    this.formData = store.get("flow_data");
    this.extra = extra;
    this.renderForm(extra, container);
    this.mailList = new MailList(document.getElementById('cclist_items'));

    utils.initJSSDK(this.jssdkconfig, ['selectEnterpriseContact'], null, null);
}

/**
 * @description 渲染处理表单
 * @param extra {object} 扩展信息
 * @param container {element|string} 扩展信息
 */
function renderForm(extra, container) {
    //TODO 需要优化页面加载等待动画
    let instance = this.formData.instance;
    let instanceId = instance.FlowInstanceGuid;
    let el = container;
    let data = {};
    let defaults = {
        contentComponent: {
            value: ''
        },
        stepComponent: {
            label: '下一处理步骤',
            show: true
        },
        principalComponent: {
            label: '下一步责任人',
            show: true
        },
        switchComponent: {
            label: '打回重走',
            show: false
        },
        ccComponent: {
            label: '抄送人', //协商人 接收人
            multiple: true,
            show: true
        }
    };

    if (container && typeof container === 'string') {
        el = document.getElementById(container.toString());
    }

    switch (extra.handleType) {
        case config.handleType.agree: //同意
            data = _.assign(defaults, {contentComponent: {value: '同意'}});
            initNextNode(instanceId);
            break;
        case config.handleType.againInitiate://重新发起
        case config.handleType.jointSignature://会签
        case config.handleType.urge://催办
        case config.handleType.recall://撤回
            data = _.assign(defaults, {contentComponent: {value: '撤回'}});
            break;
        case config.handleType.transfer: //授权交办
            data = _.assign(defaults, {
                contentComponent: {value: extra.suggestion},
                ccComponent: {
                    label: '接收人',
                    multiple: false,
                    show: true
                },
                stepComponent: {show: false},
                principalComponent: {show: false}
            });
            break;
        case config.handleType.disagree:
            if (extra.disagreeHandleType === 1) {//打回到发起
                data = _.assign(defaults, {
                    contentComponent: {value: extra.suggestion},
                    switchComponent: {show: true, label: '打回重走'}
                });
                initNextNode(instanceId, config.nextNodeType.firstStep);
            } else if (extra.disagreeHandleType === 0) {//打回到上一步
                data = _.assign(defaults, {contentComponent: {value: '打回到发起'}});
                initNextNode(instanceId, config.nextNodeType.firstStep);
            } else {
                initNextNode(instanceId);
            }
            break;
        case config.handleType.launchConsult: //发起协商
            data = _.assign(defaults, {
                contentComponent: {value: extra.suggestion},
                ccComponent: {
                    label: '接收人',
                    multiple: false,
                    show: true
                },
                stepComponent: {show: false},
                principalComponent: {show: false}
            });
            break;
        case config.handleType.cancel: //作废
            data = _.assign(defaults, {
                contentComponent: {value: extra.suggestion},
                stepComponent: {show: false},
                principalComponent: {show: false}
            });
            this.appendData = '{"WfNodeExtendType":"' + extra['netype'] + '"}';
            break;
        case config.handleType.answerAssign: //回复协商
            data = _.assign(defaults, {contentComponent: {value: ''}});
            this.appendData = '{"WfNodeExtendGuid":"' + extra['WfNodeExtendGuid'] + '"}';
            break;
        default:
            initNextNode(instanceId);
            break;
    }
    el.classList.add('weui-form-preview');
    el.classList.add('page-content');
    el.innerHTML = handleformTemplate(data);
    document.getElementById('handle_container').classList.remove('page');
    document.getElementById('handle_container').classList.add('warpper');
    this.loading && this.loading.hide();
}

/**
 * @description 获取流程处理数据
 * @returns {{FlowInstanceGuid: *, FlowHandleType: undefined|*, DisagreeHandleType: undefined|*, HandlerSuggestion, RelativePersonGuid: string|*, RelativePerson: string, ToPreviousStep: boolean, ToInitialStep: boolean, RunPathAgain: HTMLElement | boolean | * | checked, RecipientPerson: string, RecipientPersonGuid: string, FlowTemplateGuid: *, StepGuid: *, StepInstanceGuid: *, AppendData: *|((arg: string) => void)}}
 */
function getData() {
    let suggestionEl = document.getElementById('suggestion');
    let switchAgainEl = document.getElementById('switchAgain');
    let instance = this.formData.instance;
    let step = this.formData.step;
    let userIds = this.mailList.data.userList.map(user => {
        return user.id
    });
    let userNames = this.mailList.data.userList.map(user => {
        return user.name
    });
    return {
        FlowInstanceGuid: instance.FlowInstanceGuid,
        FlowHandleType: this.extra.handleType,
        DisagreeHandleType: this.extra.disagreeHandleType,
        HandlerSuggestion: suggestionEl.value,
        RelativePersonGuid: userIds.join(','),
        RelativePerson: userNames.join(','),//接收人名称
        ToPreviousStep: this.extra.disagreeHandleType === 0, //打回到上一步
        ToInitialStep: this.extra.disagreeHandleType === 1, //打回到发起
        RunPathAgain: switchAgainEl && switchAgainEl.checked,//打回重走
        RecipientPerson: '',
        RecipientPersonGuid: '',
        FlowTemplateGuid: instance.FlowTemplateGuid,
        StepGuid: step.StepGuid,
        StepInstanceGuid: step.StepInstanceGuid,
        AppendData: this.appendData
    };
}

//流程处理
function execute() {
    let loading = weui['loading']();
    try {
        let data = this.getData();
        console.log(data);
        this.validate(data);
        loading.hide();
    } catch (e) {
        weui['alert'](e.message);
        loading && loading.hide();
    }
}

//验证数据
function validate(data) {
    if (!data.HandlerSuggestion) {
        throw new Error("请输入意见");
    }
    let ccidArr = data.RelativePersonGuid.split(',');

    switch (this.currentHandleType) {
        case config.handleType.assign: //同意
            break;
        case config.handleType.answerAssign: //回复协商
            break;
        case config.handleType.againInitiate://重新发起
            break;
        case config.handleType.jointSignature://会签
            break;
        case config.handleType.urge://催办
            break;
        case config.handleType.recall://撤回
            break;
        case config.handleType.transfer: //授权交办
            if (!data.RelativePersonGuid)
                throw new Error("请选择一个接收人");
            if (ccidArr.length > 1) {
                throw new Error("只能选择一个接收人");
            }
            break;
        case config.handleType.toStart://打回到发起
            break;
        case config.handleType.toPrev://打回到上一步
            break;
        case config.handleType.launchConsult: //发起协商
            if (!data.RelativePersonGuid)
                throw new Error("请选择至少一个抄送人");
            if (!ccidArr.length)
                throw new Error("请选择至少一个抄送人");
            break;
        case config.handleType.cancel: //作废
            break;
        default:
            break;
    }
}

//初始化下一个节点信息
function initNextNode(instanceId, nextNodeType) {
    nextNodeType = nextNodeType || 0;
    $axios.get(`/flowform/getnextnodes?instanceId=${instanceId}&nextNodeType=${nextNodeType}`)
        .then(response => {
            let auditorListEl = document.getElementById('auditor');
            let stepEl = document.getElementById('step');
            let fragment = document.createDocumentFragment();
            let data = response.data || {
                StepName: "",
                StepHandMode: 0,
                Auditors: []
            };
            if (data.Auditors && data.Auditors.length) {
                data.Auditors.forEach((currValue, index, self) => {
                    let liEl = document.createElement('li');
                    let pEl = document.createElement('p');
                    liEl.classList.add('auditor');
                    if (data.StepHandMode == 2 && self.length > 1) {
                        let iEl = document.createElement('i');
                        iEl.innerText = ",";
                        liEl.appendChild(iEl);
                    }
                    pEl.innerText = currValue;
                    liEl.appendChild(pEl);
                    fragment.appendChild(liEl);
                });
                auditorListEl.innerHTML = "";//清空责任人
                auditorListEl.appendChild(fragment);
            }
            stepEl.innerHTML = data.StepName;
        })
        .catch(err => {
            console.log(err);
            alert(JSON.stringify(err));
        });
}

FlowHandle.prototype.init = init;
FlowHandle.prototype.renderForm = renderForm;
FlowHandle.prototype.getData = getData;
FlowHandle.prototype.execute = execute;
FlowHandle.prototype.validate = validate;

export default FlowHandle


