import 'es6-promise/auto'
import lodashCore from 'lodash/core'
import weui from 'weui.js'
import Http from './http'
import Identity from './identity'
import Utils from './utils'
import Config from './config'
import Form from './form'
import FlowHandle from './handle'
import FastClick from 'fastclick'
import listTemplae from './template/list.art'

const webapp = {appKey: 'mobile_approval', site: ''};

//文档加载完毕后执行
$(function () {
    //解决移动端click点击延时
    FastClick.attach(document.body);
    //导航菜单
    $('.nav-bar').on('click', '.weui-form-preview__btn', e => {
        let target = e.target;
        let data = $utils.ds(target, ['href', 'back']);
        if (target.tagName.toUpperCase() === 'A' && data.back === "False") {
            e.preventDefault();
            $utils.jumpLink(data.href);
        }
    });
});

//设置http 默认值
let http = new Http(webapp);

/**
 * @description 执行获取列表数据方法
 * @param url {string} 数据url
 * @param type {int} 列表类型
 */
function execute(url, type) {
    let title = "",
        elId = "",
        el = null;
    switch (type) {
        case Config.listType.todo:
            title = "我的待办";
            elId = 'todo';
            el = document.getElementById(elId);
            break;
        case Config.listType.application:
            title = "我的发起";
            elId = 'application';
            el = document.getElementById(elId);
            break;
        case Config.listType.transaction:
            title = "我的办理";
            elId = 'transaction';
            el = document.getElementById(elId);
            break;
        case Config.listType.cc:
            title = "我的抄送";
            elId = 'cc';
            el = document.getElementById(elId);
            break;
        default:
            break;
    }
    if (!el || !title) {
        weui.alert(`未找到 id=${elId}的html元素 作为数据容器`);
        return
    }
    let loading = weui.loading('加载中');
    $axios
        .get(url)
        .then(function (response) {
            loading.hide();
            let data = response.data;
            data.listType = type;//待办
            let html = listTemplae(data);
            el.innerHTML = html;
            Utils.setTitle(title, data.items.length);//更新网站标题上
            setTimeout(() => {
                initLinkJumpMode();
            }, 100);
        })
        .catch(function (err) {
            loading.hide();
            weui.alert(JSON.stringify(err));
        });
}

/**
 * @description 打开已办菜单
 */
function showDoneMenu() {
    weui.actionSheet(
        [
            {
                label: '我的发起',
                onClick() {
                    Utils.jumpLink("/app/application");
                }
            },
            {
                label: '我的办理',
                onClick() {
                    Utils.jumpLink("/app/transaction");
                }
            }
        ],
        [{
            label: '取消',
            onClick() {
            }
        }]
    );
}

/**
 * @description 显示子菜单
 * @param target
 */
function showSubmenu(target) {
    let data = Utils.ds(target, "submenu");
    let submenus = [];

    data.forEach(d => {
        let submenu = {
            label: d.Name,
            onClick() {
                $form.flowHandle(d);
            }
        };
        submenus.push(submenu);
    });

    weui.actionSheet(submenus, [{
        label: '取消',
        onClick() {
        }
    }]);
}

/**
 * @description 初始链接跳转方式
 */
function initLinkJumpMode() {
    var links = document.querySelectorAll('a');
    links.forEach(function (link) {
        var flag = link.getAttribute('mask');
        var href = link.href;
        if (flag === "true")
            link.addEventListener('click', e => {
                e.preventDefault();
                Utils.jumpLink(href);
            });
    });
}

//对外接口
window.$axios = http.$axios;
window.$authInstance = new Identity({webapp});
window.$utils = Utils;
window.$config = Config;
window.$_ = lodashCore;
window.$weui = weui;
window.$app = {
    execute,
    showSubmenu,
    showDoneMenu
};
window.$form = new Form();
window.FlowHandle = FlowHandle;



