import weui from 'weui.js'
import wxsdk from 'weixin-js-sdk'
import timeago from 'timeago.js'

/**
 * @description 跳转链接
 * @param {string} url 链接
 */
function jumpLink(url) {
    if (url) {
        weui.loading('加载中');
        if (url === 'BACK') {
            window.history.back();
        } else {
            window.location.href = url;
        }
    }
}

/**
 * @description 设置文档标题
 * @param {string} name 标题名称
 * @param {int} num
 */
function setTitle(name, num) {
    let titText = (num === undefined || num === -1) ? name : (name + "( " + num + " )");
    let title = document.getElementsByTagName('title');
    let bodyEl = document.body;
    let iframeEl = document.createElement('iframe');

    //iframe 加载完成执行
    function iframeLoad() {
        setTimeout(function () {
            iframeEl.removeEventListener('load', iframeLoad);
            iframeEl.remove();
        }, 0)
    }

    if (os().ios) {
        document.title = titText;
        iframeEl.src = '/Content/images/touming.png';
        iframeEl.onload = iframeLoad;
        bodyEl.appendChild(iframeEl);
        return
    }
    title[0].innerText = titText;
}

/**
 * @description 获取当前浏览器的用户代理
 */
function os() {
    let u = window.navigator.userAgent;
    let android = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //安卓
    let ios = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
    let work = !!u.match(/wxwork/); //企业微信app
    return {
        android: android,
        ios: ios,
        work
    }
}

/**
 * @description 渲染timeago时间格式
 * @param selector 选择器
 * @param fmt {function} 自定义格式函数 function(time,target)
 */
function renderTimego(selector, fmt) {
    let timeagoInstance = timeago();
    let times = document.querySelectorAll(selector);
    let tempTime;
    let now = Date.now();
    let tempTimeStr = "";
    try {
        times.forEach(time => {
            tempTime = time.getAttribute('datetime');
            if (tempTime) {
                let temp = new Date(tempTime);
                if ((now - temp.getTime()) > 86400000) {
                    tempTimeStr = tempTime.substring(0, 16);
                }
                else {
                    tempTimeStr = timeagoInstance.format(tempTime, 'zh_CN');
                }
                if (fmt instanceof Function) {
                    fmt(tempTimeStr, time);
                }
                else {
                    time["innerHTML"] = tempTimeStr;
                }
            }
        });
    } catch (e) {
        console.error(e)
    }
}

/**
 * @description 判断是否为常用office文件
 * @param suffix
 * @returns {boolean}
 */
function isOffice(suffix) {
    let flag = false;
    let office = ['.doc', '.docx', '.ppt', '.pptx', '.xls', '.xlsx',];
    if (suffix) {
        flag = !!office.find(o => o === suffix.toLowerCase());
    }
    return flag;
}

/**
 * @description 获取DOM元素的dataset数据
 * @param target DOM元素
 * @param keys {string|Array} 数据键
 * @returns {*}
 */
function dataset(target, keys) {
    let result = {};
    let prefix = "";
    if (typeof keys === 'string') {
        keys = [keys];
    }
    let source = target.attributes;
    if (typeof target.dataset === 'object') {
        source = target.dataset;
    } else {
        prefix = "data-";
    }
    keys.forEach(key => {
        let temp = source[prefix + key];
        try {
            if (temp !== undefined) {
                temp = JSON.parse(temp);
            }
        }
        catch (e) {
        }
        result[key] = temp;
    });

    if (keys.length === 1) {
        return result[keys[0]];
    } else {
        return result;
    }
}

/**
 * @description 强制刷新缓存页面 解决新版本ios浏览器返回时不执行JavaScript问题
 * @param source window
 */
function forceReload(source) {
    if (source && source.event.persisted && os().ios) {
        window.location.reload(true)
    }
}

/**
 * @description 初始化JSSDK
 * @param jssdkconfig {object} 配置对象
 * @param apiList {Array} api列表
 * @param ready {Function}
 * @param error {Function}
 */
function initJSSDK(jssdkconfig, apiList, ready, error) {
    //TODO 初始JSSDK 待优化 url 不变则不用重新初始jssdk
    let defaults = {
        beta: true,// 必须这么写，否则在微信插件有些jsapi会有问题
        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        jsApiList: apiList // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
    };
    let options = _.assign(defaults, jssdkconfig);
    wxsdk.config(options);
    wxsdk.ready(function () {
        // config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
        ready instanceof Function && ready();

    });
    wxsdk.error(function (res) {
        // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
        error instanceof Function && error(res);
    });
}

export default {
    jumpLink,
    setTitle,
    os,
    ds: dataset,
    forceReload,
    initJSSDK,
    isOffice,
    renderTimego
}


