import weui from 'weui.js'
import utils from './utils'

/**
 * @description 身份认证 构造函数
 * @constructor
 */
function Identity(webapp) {
    this.appKey = webapp.appKey;
    this.site = webapp.site;
    this.tokenPoint = webapp.site + "/wechat/authorize";
}

/**
 * @description 授权
 * @private
 */
function _authorize() {
    let loading;
    try {
        loading = weui.loading();
        if (!this.verifyToken(this.appKey)) {
            utils.jumpLink(this.tokenPoint);
        } else {
            loading.hide();
        }
    } catch (e) {
        console.error(e);
        loading && loading.hide();
        weui.alert(e.message, {title: '警告'});
    }
}

/**
 * @description 认证
 * @private
 */
function _authenticate() {
    throw new Error("未实现的方法");
}

/**
 * @description 验证token是否有效
 * @param key
 * @returns {boolean}
 * @private
 */
function _verifyToken(key) {

    let flag = false;
    let nowTimeStamp = Date.now();
    try {
        let token = this.getToken(key);
        if (token && token.expires_in) {
            if (token.expires_in > nowTimeStamp) {
                flag = true;
            } else {
                //身份过期 通过刷新令牌重新获取
            }
        }
    } catch (e) {
        console.error(e)
    }
    return flag;
}

/**
 * @description 保存令牌信息
 * @param key
 * @param token
 * @private
 */
function _saveToken(key, token) {
    let tokenStr = token;
    if (typeof token === 'object') {
        tokenStr = JSON.stringify(token);
    }
    localStorage.setItem(key, tokenStr);
}

/**
 * @description 获取令牌信息
 * @param key
 * @returns {*}
 * @private
 */
function _getToken(key) {
    try {
        let tokenStr = localStorage.getItem(key);
        return JSON.parse(tokenStr);
    } catch (e) {
        return {
            expires_in: undefined
        };
    }
}

/**
 * @description 删除令牌信息
 * @param key
 * @private
 */
function _removeToken(key) {
    localStorage.removeItem(key);
}

Identity.prototype = {
    constructor: Identity,
    getToken: _getToken,
    saveToken: _saveToken,
    removeToken: _removeToken,
    verifyToken: _verifyToken,
    authorize: _authorize,
    authenticate: _authenticate
};

export default Identity


